<template>
  <div class="personal">
    <el-container>
      <el-aside width="200px " class="lightest-bg">
        <el-menu
          :default-active="this.$route.path"
          class="el-menu-vertical-demo"
          active-text-color="#333333"
          :router="true"
          @select="handleClick"
        >
          <el-menu-item :index="item.name" v-for="(item,i) in navList" :key="i">
            <span slot="title">{{item.navItem}}</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view ></router-view>
      </el-main>
    </el-container>
  </div>
</template>
<script>
export default {
  data() {
    return {
      navList: [
        { name: "/personal/message", navItem: "消息中心" },
        { name: "/personal/collection", navItem: "收藏" },
        { name: "/personal/printing", navItem: "倡印" },
        { name: "/personal/wechat", navItem: "微信绑定" },
        { name: "/personal/iphone", navItem: "手机绑定" },
        { name: "/personal/datasete", navItem: "个人资料" },
        { name: "/personal/address", navItem: "收货地址" }
      ],
    };
  },
  mounted() {},
  methods: {
    handleClick(index) {
      if (index == "/personal/collection") {
        var _this = this;
        var url = this.APIURL + "/classic/usr/collect/all";
        var data = {
          pageSize: 1000,
          type: "classic"
        };
        this.$axios
          .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
          .then(res => {
            // console.log(res.data.data.dataSet)
            this.$store.commit("setCollectionClassic", res.data.data.dataSet)
          })
          .catch(err => {});
      }
    }
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  }
};
</script>
<style lang="scss">
.personal {
  width: 1200px;
  margin: 20px auto 0;
  .el-aside {
    border-right: solid 1px #e6e6e6;
  }
  .el-main {
    margin-left: 20px;
  }
  .el-menu {
    border: none;
  }
  .el-menu-item {
    padding-left: 50px !important;
  }
  .el-menu-item.is-active {
    border-left: 3px solid #584945;
    background: #f9f8f4;
  }
  .el-menu-item:hover {
    background-color: #f9f8f4;
  }
}
</style>
